import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'chuck';

  chucks:any = []
  categories: any =[]

  research: any = []

  chuckTest:any = null

  chucksDuFutur= [
    {"categories":[],"created_at":"2020-01-05 13:42:29.569033","icon_url":"https://assets.chucknorris.host/img/avatar/chuck-norris.png","id":"0BGTdZTfQkaUdgh53Sse6Q","updated_at":"2020-01-05 13:42:29.569033","url":"https://api.chucknorris.io/jokes/0BGTdZTfQkaUdgh53Sse6Q","value":"The rock band Stone Temple Pilots got their name after they witnessed Chuck Norris cruising around in an Aztec shrine on the streets of L.A."},
    {"categories":[],"created_at":"2020-01-05 13:42:29.296379","icon_url":"https://assets.chucknorris.host/img/avatar/chuck-norris.png","id":"pNUF3mtZTmyVsoGLvbtM3A","updated_at":"2020-01-05 13:42:29.296379","url":"https://api.chucknorris.io/jokes/pNUF3mtZTmyVsoGLvbtM3A","value":"Man, I thought it was gonna rain today but Chuck Norris come&#65279; down and roundhouse kicked the weatherman."},
    {"categories":[],"created_at":"2020-01-05 13:42:29.569033","icon_url":"https://assets.chucknorris.host/img/avatar/chuck-norris.png","id":"9buwTSw7QaSQddvOTPEz_g","updated_at":"2020-01-05 13:42:29.569033","url":"https://api.chucknorris.io/jokes/9buwTSw7QaSQddvOTPEz_g","value":"Little known fact: Talking Shit About Chuck Norris is the eighth Deadly Sin. And by far the most deadly."}
  ]


  url: string = "https://api.chucknorris.io/jokes/random"
  hide(){
    this.chucks = []
  }

  getCategorie(){
    this.client.get('https://api.chucknorris.io/jokes/categories').subscribe(data =>{
      this.categories = data
    })
  }

  getjokes(){
    this.client.get(this.url).subscribe(chuckReponse =>{this.chucks.push(chuckReponse)})

  }
  getJokesByCategorie(categorie: string ){
    this.client.get(this.url+'?category='+categorie).subscribe(chuckReponse =>{this.chucks.push(chuckReponse)})

  }

  getJokesBySearch(search: string){
    this.client.get('https://api.chucknorris.io/jokes/search?query='+search).subscribe((data:any) => this.chucks = data.result)

  }

  suppr(aSupprimer: object){

    this.chucks.splice(this.chucks.indexOf(aSupprimer), 1)

  }

  constructor(private client: HttpClient){

  }

  ngOnInit(){
    this.getCategorie();
  }
}
